<template>
  <div class="login-page">
    <div class="main-container container-flex">
      <div class="col-one col50">
        <h2>¡Bienvenido!</h2>
        <p>El éxito es la suma de pequeños esfuerzos día a día.</p>
        <hr>
      </div>
      <div class="col-two col50">
        <div class="col-logo">
          <img src="../assets/logo.jpg">
        </div>
        <div class="form-container">
          <h4>Login</h4>
          <form id="loginapp"   @submit.prevent="login" action="" method="post">  
            <div class="row-block">
              <p for="correo">Correo</p>
              <input 
                  id="correo" 
                  v-model="email_form" 
                  type="email" 
                  name="correo" 
                  required
                  placeholder="info@legrafica.mx"
              >
            </div>
            <div class="row-block">
              <p for="password">Contraseña</p>
              <input 
                  id="password" 
                  v-model="password_form" 
                  type="password" 
                  name="password" 
                  required
                  placeholder="**************"              
              >
            </div>
            <p v-if="status=='error'" class="error">Has introducido mal el email o la contraseña.</p>
            <div class="btn-login">
              <input type="submit" value="Iniciar Sesión">
            </div>
          </form>
        </div>
        <p class="txt-version">V 1.0.0</p>
      </div>
    </div>
  </div>
</template>
<script>

export default {
  name: 'login',
  data: () => ({
    email_form: "",
    password_form: "",
    status:'',
  }),
  computed:{
    token() {
      return this.$store.getters['userService/getToken'];
    },
    identity() {
      return this.$store.getters['userService/getIdentity'];
    }
  },
  methods: {
    async login() {
      try {
        let response_token = await this.$store.dispatch("userService/login", {
            email: this.email_form,
            password: this.password_form,
            gettoken: true
        });
        
        if (response_token.status != 'error') {
          this.status = 'success';
          this.$store.dispatch("userService/setToken", this.token);          
          this.$store.dispatch("userService/getToken");
          this.$store.dispatch("userService/decode", this.token);

          this.$router.push("/auth").catch(err => {});
          console.log(this.identity)
        } else {
          this.status = 'error';
        }
       
      } catch (error) {
        this.status ='error'
      }
    }
  }
}
</script>


